.is-hidden {
  display: none;
}

body {
  background-color: #def3f3;
}

.cat-info {
  text-align: center;
  background-color: #d8f3fb;
  border: 7px ridge #77cff5;
  border-radius: 15px;
  margin: 5px;
  padding: 40px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 90px;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.bouncer {
  justify-content: space-around;
  align-items: flax-end;
  max-width: 200px;
  max-height: 100px;
  margin: 200px auto 0;
  display: flex;
}

.bouncer div {
  width: 20px;
  height: 20px;
  background-color: #07f;
  border-radius: 50px;
  animation: bouncer .5s cubic-bezier(.19, .57, .3, .98) infinite alternate;
}

.bouncer div:nth-child(2) {
  opacity: .8;
  animation-delay: .2s;
}

.bouncer div:nth-child(3) {
  opacity: .6;
  animation-delay: .3s;
}

.bouncer div:nth-child(4) {
  opacity: .4;
  animation-delay: .4s;
}

@keyframes bouncer {
  from {
    transform: translateY(0);
  }

  to {
    transform: translateY(-100px);
  }
}

.spinner {
  width: 100px;
  height: 100px;
  position: relative;
}

.spinner div {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border: 8px solid rgba(0, 0, 0, 0);
  border-top-color: #f332bc;
  border-radius: 50%;
  animation: spinnerOne 1.2s linear infinite;
  position: absolute;
}

.spinner div:nth-child(2) {
  border: 10px solid rgba(0, 0, 0, 0);
  border-bottom-color: #ce75f1;
  animation: spinnerTwo 1s linear infinite;
}

@keyframes spinnerOne {
  0% {
    border-width: 10px;
    transform: rotate(0deg);
  }

  50% {
    border-width: 1px;
    transform: rotate(180deg);
  }

  100% {
    border-width: 10px;
    transform: rotate(360deg);
  }
}

@keyframes spinnerTwo {
  0% {
    border-width: 1px;
    transform: rotate(0deg);
  }

  50% {
    border-width: 10px;
    transform: rotate(180deg);
  }

  100% {
    border-width: 1px;
    transform: rotate(360deg);
  }
}

